.calculator {
	overflow: hidden;


	& .ruler {
		// background-image: url(../img/ruler-cm.gif);
		// background-repeat: repeat-x;
		max-height: 481px;
		margin-bottom: 10vh;

		&__inner {
			max-width: 1440px;
			box-sizing: border-box;
			margin: 0 auto;
			background-image: url(../img/ruler-cm.gif);
			background-repeat: repeat-x;
			position: relative;
			z-index: 100;
			background-position-x: 0;
			display: flex;
			flex-flow: row nowrap;
			align-items: center;

			&::before, &::after {
				content: '';
				display: block;
				position: absolute;
				height: 100%;
				width: 200vw;
				background-image: url(../img/ruler-cm.gif);
				background-repeat: repeat-x;
				background-size: contain;
				top: 0;
			}

			&::before {
				right: 100%;
				background-position: top right;
			}

			&::after {
				left: 0;
				z-index: -1;
				background-position: top left;
			}
		}

		&-info img, &-gif img {
			@include img-fluid();
		}

		&-info {
			display: none;
		}
	}

	// .plant {
	// 	display: none;
	// 	transition: right .4s ease;
	// }

	&__pieces {
		width: 100%;
		box-sizing: border-box;
		padding: 0 6.5vw;
		display: flex;
		flex-flow: row wrap;
		justify-content: flex-start;
		margin-bottom: 10vh;
	}

	&__pieces-item {
		width: 100%;
		margin: 0 auto 3vh;
		font-size: $fz-sm;
		box-sizing: border-box;
	}

	&__pieces-item img {
		@include img-fluid();
		max-width: 104%;
		margin: 0 -2% 1vh;
	}

	&__pieces-item figcaption {
		color: #666;
	}
}





@media ($min-md) {
	.calculator {
		.ruler {
			margin-bottom: 12vh;

			&__inner {
				padding-right: 6.5vw;
			}

			&-info {
				display: block;
			}

			&__inner.rulerAppear {
				visibility: visible !important;
				animation: rulerAppear 1s .2s cubic-bezier(0.18, 0.89, 0.32, 1.28) 1 both;
				animation-play-state: paused;
			}

			&__inner.rulerAppear.animated {
				animation-play-state: running;
			}

			&-info {
				animation: fadeInDown .4s .2s ease-out 1 both;
				animation-play-state: running;
			}
		}

		// .plant {
		// 	display: block;
		// 	position: absolute;
		// 	width: 16vw;
		// 	height: auto;
		// 	max-width: 198px;
		// 	right: -6%;
		// 	bottom: -10%;
		//
		// 	& img {
		// 		@include img-fluid();
		// 	}
		// }

		&__pieces {
			align-items: flex-end;
			margin: 0 auto 10vh;
		}
	}
}

@media ($min-lg) {
	.calculator {

		// .plant {
		// 	width: 14vw;
		// 	right: -2%;
		// }

		&__pieces {
			padding: 0 2%;
		}

		&__pieces-item {
			width: 50%;
			flex: 1 1 50%;
			padding: 0 20px;

			&:nth-child(2) {
				align-self: flex-start;
			}

			&:nth-child(4) {
				margin-top: -6%;
			}
		}
	}
}

@media ($min-xl) {
	.calculator{
		.ruler{
			&__inner{
				&::before, &::after{
					background-size: auto;
				}
			}
		}

		// .plant {
		// 	right: -1vw;
		// }
	}
}


